import React, {useEffect, useState} from 'react';
import {useForm} from 'react-hook-form';
import {Box, Button, Modal, TextField} from "@mui/material";
import {style} from "app/shared/util/modal-style";
import {useAppDispatch, useAppSelector} from "app/config/store";
import {login} from "app/shared/reducers/authentication";
import {Close} from "@mui/icons-material";
import {toast} from "react-toastify";
import {openRegisterModal, resetModals} from "app/shared/components/login/login-modal.reducer";

const LoginForm = () => {
  const isAuthenticated = useAppSelector(state => state.authentication.isAuthenticated);
  const dispatch = useAppDispatch();
  const loginError = useAppSelector(state => state.authentication.loginError);
  const loginModalRequested = useAppSelector(state => state.loginModal.loginModalRequested);
  const [modalOpen, setModalOpen] = useState(false);
  const handleLogin = (username, password, rememberMe = false) => dispatch(login(username, password, rememberMe));

  useEffect(() => {
    if (loginError) {
      toast.error("Sign in failed!")
    }
  }, [loginError]);

  useEffect(() => {
    if (loginModalRequested) {
      setModalOpen(true)
      dispatch(resetModals())
    }
  }, [loginModalRequested]);

  const {
    register,
    getValues,
    reset,
    formState: {errors},
  } = useForm({mode: 'onTouched'});

  const onSubmit = (event) => {
    event.preventDefault()
    handleLogin(getValues('login'), getValues('password'), true);
  };

  const openRegister = () => {
    setModalOpen(false)
    dispatch(openRegisterModal())
  }

  return (
    <>
      <Modal
        disableEnforceFocus
        open={modalOpen && !isAuthenticated}
        onClose={() => {
          setModalOpen(false)
          reset()
        }}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description">
        <Box sx={style}>
          <form onSubmit={onSubmit} className="space-y-2">
            <div className="flex justify-between items-center mb-2">
              <div>
              </div>
              <h3 className="font-semibold text-lg">

                Sign in
              </h3>
              <div className="cursor-pointer" onClick={() => setModalOpen(false)}>
                <Close></Close>
              </div>
            </div>
            <TextField
              required
              id="login"
              name="login"
              autoFocus
              color="secondary"
              label="Username or e-mail"
              type="text"
              fullWidth
              {...register('login')}
              error={!!errors.login}
            />
            <p>
              {errors.newPassword?.message}
            </p>
            <TextField
              required
              id="password"
              name="password"
              color="secondary"
              type="password"
              label="Password"
              fullWidth
              {...register('password')}
              error={!!errors.password}
            />
            <div>
              {errors.password?.message}
            </div>
            <Button
              className="w-full"
              variant="contained"
              onClick={() => setModalOpen(true)}
              type="submit">
              Sign in
            </Button>
          </form>
        </Box>
      </Modal>
    </>
  );
};

export default LoginForm;
