import './header.scss';

import React, {useEffect} from 'react';
import {useNavigate} from "react-router-dom";
import {useAppDispatch, useAppSelector} from "app/config/store";
import {getSession} from "app/shared/reducers/authentication";
import {Avatar, Button, Divider, Drawer, ListItemIcon, MenuItem} from "@mui/material";
import {When} from "react-if";
import {hasAnyAuthority} from "app/shared/auth/private-route";
import {AUTHORITIES} from "app/config/constants";
import {AdminPanelSettings, House, Logout, MessageOutlined, Security} from "@mui/icons-material";
import LoginForm from "app/shared/components/login/login-form";
import LoginFormButton from "app/shared/components/login/login-form-button";
import {scrollTop} from "app/shared/util/helper-utils";

const Header = () => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const [drawerOpen, setDrawerOpen] = React.useState(false);

  useEffect(() => {
    dispatch(getSession());
  }, []);

  const account = useAppSelector(state => state.authentication.account);
  const isAuthenticated = useAppSelector(state => state.authentication.isAuthenticated);
  const isAuthLoading = useAppSelector(state => state.authentication.loading);
  const isAdmin = useAppSelector(state => hasAnyAuthority(state.authentication.account.authorities, [AUTHORITIES.ADMIN]));


  return (
    <>
      <nav className="sticky top-0 bg-gray-300  backdrop-filter backdrop-blur-lg bg-opacity-40 px-4 xl:mx-0 z-50">
        <LoginForm></LoginForm>
        <div className="mx-auto">
          <div className="flex justify-between h-14 self-end">
            <div className="flex space-x-4 text-gray-900 items-center">
              <div className="self-center font-semibold cursor-pointer logo-text"
                   onClick={() => {
                     navigate('/')
                     scrollTop();
                   }}>Tootmon
              </div>
            </div>
            <div className="hidden sm:flex space-x-4 items-center text-gray-900 ">
              <When condition={isAuthenticated && !isAuthLoading}>
                <LoginFormButton isDrawer={false}></LoginFormButton>

              </When>
              <When condition={!isAuthenticated && !isAuthLoading}>
                <Button variant="outlined" onClick={() => navigate("/dashboard")}>
                  <p className="font-semibold">
                    Tlaud
                  </p>
                </Button>
              </When>
            </div>
          </div>
        </div>
        <Drawer
          anchor="left"
          open={drawerOpen}
          onClose={() => setDrawerOpen(false)}
        >
          <div className="px-3 pt-3 drawer-content">
            <When condition={isAuthenticated}>
              <MenuItem
                onClick={() => {
                  navigate('/account/' + account.login);
                  setDrawerOpen(false);
                }}>
                <Avatar alt={account?.imageUrl}
                        src={account?.imageUrl}/>
                <p className="ml-2 text-md font-semibold uppercase">
                  My account
                </p>
              </MenuItem>
              <Divider/>
            </When>
            <MenuItem
              onClick={() => {
                setDrawerOpen(false)
                navigate('/')
              }
              }>
              <ListItemIcon>
                <House color="primary" fontSize="small"/>
              </ListItemIcon>
              <p>
                Home
              </p>
            </MenuItem>
            <When condition={isAuthenticated}>
              <MenuItem onClick={() => {
                setDrawerOpen(false)
                navigate('/messages')
              }}>
                <ListItemIcon>
                  <MessageOutlined color="primary" fontSize="small"/>
                </ListItemIcon>
                Messages
              </MenuItem>
            </When>
            <Divider/>
            <When condition={isAuthenticated}>
              <MenuItem onClick={() => {
                setDrawerOpen(false)
                navigate('/account/change/password')
              }}>
                <ListItemIcon>
                  <Security color="primary" fontSize="small"/>
                </ListItemIcon>
                Security
              </MenuItem>
            </When>
            <When condition={isAdmin}>
              <MenuItem onClick={() => {
                setDrawerOpen(false)
                navigate('/admin/content-creation')
              }}>
                <ListItemIcon>
                  <AdminPanelSettings color="primary"/>
                </ListItemIcon>
                Admin
              </MenuItem>
            </When>
            <When condition={isAuthenticated}>
              <MenuItem onClick={() => {
                navigate('/logout')
                setDrawerOpen(false)
              }}>
                <ListItemIcon>
                  <Logout color="primary" fontSize="small"/>
                </ListItemIcon>
                Sign out
              </MenuItem>
            </When>
          </div>
        </Drawer>
      </nav>
    </>
  )
};

export default Header;
