import React from 'react';
import {Button, ListItemIcon, MenuItem} from "@mui/material";
import {useAppDispatch, useAppSelector} from "app/config/store";
import {Else, If, Then, When} from "react-if";
import {LoginOutlined} from "@mui/icons-material";
import {openLoginModal} from "app/shared/components/login/login-modal.reducer";

const LoginFormButton = (props: { isDrawer?: boolean }) => {
  const isAuthenticated = useAppSelector(state => state.authentication.isAuthenticated);
  const dispatch = useAppDispatch();
  return (
    <>
      <When condition={!isAuthenticated}>
        <If condition={!props?.isDrawer}>
          <Then>
            <Button variant="outlined" onClick={() => dispatch(openLoginModal())}>
              <p className="font-semibold">
                Sign in
              </p>
            </Button>
          </Then>
          <Else>
            <MenuItem onClick={() => dispatch(openLoginModal())}>
              <ListItemIcon>
                <LoginOutlined color="primary" fontSize="small"/>
              </ListItemIcon>
              Sign in
            </MenuItem>
          </Else>
        </If>
      </When>
    </>
  )
};

export default LoginFormButton;
